import * as types from '../../plugins/helper/mutation-types';
/* eslint-disable no-console */
const vuetify = {
  namespaced: true,
  state: {
    // 上方提示視窗
    alert: {
      type: undefined,
      value: false,
      outlined: false,
      dismissible: false,
      transition: undefined,
      message: '',
    },
    // 上方讀取進度條
    progressLinear: {
      active: false,
      indeterminate: false,
      color: 'yellow accent-4',
    },
    openLoding: {},
    closeLoding: {},
  },
  mutations: {
    [types.MODIFY_ALERT](state, payload) {
      state.alert = payload;
    },
    [types.MODIFY_PROGRESS_LINEAR](state, payload) {
      state.progressLinear.active = payload.active;
      state.progressLinear.indeterminate = payload.indeterminate;
    },
  },
  actions: {
    [types.MODIFY_ALERT]({ commit }, payload) {
      commit(types.MODIFY_ALERT, payload);
    },
    [types.MODIFY_PROGRESS_LINEAR]({ commit }, payload) {
      commit(types.MODIFY_PROGRESS_LINEAR, payload);
    },
    // 在module裡面取得根狀態 rootstate
    test({ rootState }) {
      console.log(rootState.guard.Authenticate);
    },
  },
  getters: {},
};


export default vuetify;
