import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import store from '../store';
import * as types from '../plugins/helper/mutation-types';

Vue.use(VueRouter);


const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/About.vue'),
  },
  {
    path: '/member',
    name: 'member',
    component: () => import('../views/Member.vue'),
    meta: { requiresGuest: true },
  },
  {
    path: '/api/login/:provider/callback',
    props: route => ({ query: route.query }),
    name: 'callback',
    component: () => import('../components/HandleProviderCallback.vue'),
  },
  {
    path: '/admin/profile',
    name: 'profile',
    component: () => import('../views/admin/Profile.vue'),
    meta: { requiresAuth: true },
  },

];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});


router.beforeEach((to, from, next) => {
  // 如果有requiresAuth則走下面這條驗證
  if (to.matched.some(record => record.meta.requiresAuth)) {
    /**
     * 如果自定義驗證不通過則返回login頁面
     * 這邊設定了延遲0.1秒,好讓刷新頁面時可以正確讀取到localsotrage
     * 如果localstorage被刪除強制登出
     */
    setTimeout(() => {
      if (!store.state.guard.Authenticate
        || !localStorage.getItem(types.GD)
        || !localStorage.getItem(types.PROFILE)
      ) {
        store.dispatch(types.LOGOUT);
        next({
          path: '/',
          query: { redirect: to.fullPath },
        });
      } else {
        // 如果驗證通過則進去登入允許的畫面,設定store authenticate=true
        next();
      }
    }, 100);
    // 如果有requiresGuest則走下面這條驗證
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    setTimeout(() => {
      if (store.state.guard.Authenticate) {
        next({
          path: '/',
          query: { redirect: to.fullPath },
        });
      } else {
        // 如果驗證通過則進去登入允許的畫面,設定store authenticate=true
        next();
      }
    }, 100);
  } else {
    // 如果沒有任何路由需要驗證 則走下面這條正常next
    next();
  }
});

export default router;
