
import Vue from 'vue';
import Vuex from 'vuex';
import * as types from '../plugins/helper/mutation-types';
import Storage from '../plugins/cryptostorage';
import modulesA from './modules/vuetify';


Vue.use(Storage);
Vue.use(Vuex);
/* eslint-disable no-console */
/* eslint-disable no-underscore-dangle */
/* eslint-disable no-undef */
export default new Vuex.Store({
  state: {
    guard: {
      Authenticate: false,
      token_type: '',
      expires_in: 0,
      access_token: '',
      refresh_token: '',
    },
    // user data
    profile: {
      id: '',
      name: '',
      email: '',
      email_verified_at: '',
      mobile: '',
      mobile_verified_at: '',
      level: '',
      created_at: '',
      updated_at: '',
    },
  },
  mutations: {
    // 這通常用在請求登入 第一次設置token時候使用,或者重新請求驗證設置
    [types.SET_GUARD](state, guard) {
      state.guard.Authenticate = guard.Authenticate;
      state.guard.token_type = `${guard.token_type} `;
      state.guard.expires_in = guard.expires_in;
      state.guard.access_token = guard.access_token;
      state.guard.refresh_token = guard.refresh_token;
      this._vm.$_storage_set(localStorage, types.GD, guard);
    },
    // 防止資料消失,重新讀取storage並賦予給state
    [types.INIT_LOGINSTATUS](state, payload) {
      state.guard.Authenticate = payload.deCryptGuard.Authenticate;
      state.guard.token_type = `${payload.deCryptGuard.token_type} `;
      state.guard.expires_in = payload.deCryptGuard.expires_in;
      state.guard.access_token = payload.deCryptGuard.access_token;
      state.guard.refresh_token = payload.deCryptGuard.refresh_token;
      state.profile = payload.deCryptProfile;
    },

    // 設定使用者資料到localstorage
    [types.SET_PROFILE](state, profile) {
      state.profile = profile;
      this._vm.$_storage_set(localStorage, types.PROFILE, profile);
    },

    // 登出
    [types.LOGOUT](state) {
      state.guard.Authenticate = false;
      state.guard.token_type = '';
      state.guard.expires_in = 0;
      state.guard.access_token = '';
      state.guard.refresh_token = '';
      this._vm.$_storage_set(localStorage, types.GD, state.guard);
    },
  },
  actions: {

    [types.SET_GUARD]({ commit }, guard) {
      commit(types.SET_GUARD, guard);
    },

    [types.SET_PROFILE]({ commit }, profile) {
      commit(types.SET_PROFILE, profile);
    },

    // 拿refresh_token 換取新的access_token、refresh_token
    [types.REFRESH_TOKEN_CHANGE_NEW_TOKEN]({ state, dispatch }) {
      Vue.axios({
        method: 'post',
        baseURL: types.BASE_URL,
        url: '/api/change/new/token',
        data: { refresh_token: state.guard.refresh_token },
      }).then((response) => {
        // 如果使用refresh_token取得到新的token則commit更新guard
        const guard = response.data;
        guard.Authenticate = true;
        console.log(guard);
        dispatch(types.SET_GUARD, guard);
        setTimeout(() => {
          window.location.reload();
        }, 2000);
      }).catch(() => {
        dispatch(types.LOGOUT);
      });
    },

    /**
     * 組件內初始化判斷storage裡面guard是否認證通過
     * 要判斷兩個數值,passport/profile
     */
    [types.INIT_LOGINSTATUS]({ commit }) {
      const guardIsSet = window.localStorage.getItem(types.GD);
      const profileIsSet = window.localStorage.getItem(types.PROFILE);
      const payload = {};
      payload.deCryptGuard = this._vm.$_storage_get(localStorage, types.GD);
      payload.deCryptProfile = this._vm.$_storage_get(localStorage, types.PROFILE);
      if (guardIsSet && profileIsSet && _.eq(payload.deCryptGuard.Authenticate, true)) {
        commit(types.INIT_LOGINSTATUS, payload);
      } else {
        commit(types.LOGOUT);
      }
    },
    // 登出
    [types.LOGOUT]({ commit }) {
      commit(types.LOGOUT);
    },
  },
  modules: {
    [types.VUETIFY_PATH]: modulesA,
  },
});
