<script>
/* eslint-disable no-console */
// import { createNamespacedHelpers } from 'vuex';
// const { mapState } = createNamespacedHelpers('vuetify');
import { mapState } from 'vuex';
import * as types from './plugins/helper/mutation-types';

export default {
  name: 'App',
  data() {
    return {
      registerForms: {
        name: '',
        email: '',
        password: '',
        password_confirmation: '',
        mobile: '',
        platform: '',
        station: '',
      },
      forms: {
        username: '',
        password: '',
        scope: '',
      },
    };
  },
  created() {
    // console.log(process.env.NODE_ENV);
  },
  computed: {
    ...mapState('vuetify', {
      alert: state => state.alert,
      progressLinear: state => state.progressLinear,
    }),
    ...mapState({
      Authenticate: state => state.guard.Authenticate,
      email: state => state.profile.email,
    }),
  },
  methods: {
    login() {
      this.axios({
        method: 'post',
        baseURL: types.BASE_URL,
        url: '/oauth/token',
        data: this.forms,
      }).then((response) => {
        console.log(response.data);
      });
    },
    register() {
      this.axios({
        method: 'post',
        baseURL: types.BASE_URL,
        url: '/api/register',
        data: this.registerForms,
      }).then((response) => {
        console.log(response.data);
      }).catch((error) => {
        console.log(error.response.data);
      });
    },
    AuthProvider(provider) {
      const self = this;
      this.$auth.authenticate(provider).then((response) => {
        self.SocialLogin(provider);
        console.log(response);
      }).catch((err) => {
        console.log(err.response.data);
      });
    },
    SocialLogin(provider) {
      this.axios({
        method: 'post',
        baseURL: types.BASE_URL,
        url: `/sociallogin/${provider}`,
      }).then((response) => {
        console.log(response);
      }).catch((err) => {
        console.log(err.response.data);
      });
    },
  },
};
</script>
