import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import App from './App.vue';
import './registerServiceWorker';
import router from './router';
import store from './store';
import vuetify from './plugins/vuetify';
import Storage from './plugins/cryptostorage';
import * as types from './plugins/helper/mutation-types';

Vue.use(Storage);
Vue.use(VueAxios, axios);
Vue.config.productionTip = true;

/* eslint-disable no-console */
/* eslint-disable no-param-reassign */
/* eslint-disable no-undef */
/* eslint-disable max-len */

// 全域攔截器,只針對撈取資料驗證專用
Vue.axios.interceptors.request.use((config) => {
  config.headers.common.Authorization = `${store.state.guard.token_type} ${store.state.guard.access_token}`;
  config.headers.common['Content-Type'] = 'application/json';
  // console.log(typeof (config.data));
  return config;
},
(error) => {
  console.log(error);
});

Vue.axios.interceptors.response.use(response => response,
  (error) => {
    switch (error.response.status) {
      /**
         * 1.401
         * 2.如果發出請求收到401代碼,先用refresh_token 去換access_token
         */
      case 401:
        store.dispatch(`vuetify/${types.MODIFY_PROGRESS_LINEAR}`, { active: true, indeterminate: true });
        setTimeout(() => {
          store.dispatch(types.REFRESH_TOKEN_CHANGE_NEW_TOKEN);
        }, 2000);
        break;
        /**
         * 狀態422
         */
      case 422:
        console.log(error.response.status);
        store.dispatch(types.LOGOUT).then(() => {
          router.push({ name: 'member' });
        });
        break;
        /**
         * 狀態429 請求數過多遭到限制
         */
      case 429:
        console.log('請求次數過多,10分鐘後操作!');
        break;
      case 500:
        console.log('Server端錯誤!!');
        break;
      default:
        console.log('interceptors response default');
    }
    return error;
  });

new Vue({
  router,
  store,
  vuetify,
  data() {
    return {};
  },
  created() {
    // 避免重新整理資料消失
    store.dispatch(types.INIT_LOGINSTATUS);
  },
  render: h => h(App),
}).$mount('#app');
