/**
 * 【 CryptoStorage 】 2019/11/11 Version 1.0.0
 * encrypt/decrypt LocalStorage、SessionStorage in vue
 *
 */

/* eslint-disable import/no-unresolved */
import _ from 'lodash';
import CryptoJS from 'crypto-js';
import * as types from './lib/config';

/* eslint-disable class-methods-use-this */
/* eslint-disable no-unused-vars */
/* eslint-disable consistent-return */
/* eslint-disable no-unused-expressions */
class Storage {
  install(Vue, options = {}) {
    Vue.mixin({

      methods: {

        /**
                 *
                 *  【 SET 】 encrypt localStorage/sessionStorage
                 *
                 */
        $_storage_set(method, k, v) {
          if (method !== localStorage && sessionStorage && !_.isObject(method)) {
            return false;
          }

          const ciphertext = CryptoJS.AES.encrypt(JSON.stringify(v), types.PUBLIC_KEY);
          method === localStorage ? localStorage : sessionStorage;
          method.setItem(k, ciphertext);
        },


        /**
                 *
                 *  【 GET 】 decrypt localStorage/sessionStorage
                 *
                 */
        $_storage_get(method, k) {
          if (method !== localStorage && sessionStorage && !_.isObject(method)) {
            return false;
          }

          const ciphertext = method.getItem(k);
          if (!_.isNull(ciphertext) && ciphertext.length > 30) {
            const bytes = CryptoJS.AES.decrypt(ciphertext.toString(), types.PUBLIC_KEY);
            const data = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));

            return data;
          } if (_.isNull(ciphertext)) {
            method.removeItem(k);
          } else {
            method.removeItem(k);
          }


          return false;
        },


        /**
                 *
                 *  【 REMOVE 】 remove localStorage/sessionStorage
                 *
                 */
        $_storage_remove(method, k) {
          if (method !== localStorage && sessionStorage && !_.isObject(method)) {
            return false;
          }


          method === localStorage ? localStorage : sessionStorage;
          method.removeItem(k);
        },
      },

    });
  }
}


export default new Storage();
